<template>
  <div class="my-order-view ">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <div class="scorll-tab">
      <tab-view :action="tabAction" @changeTab="changeTab" :activeId="active"></tab-view>
    </div>
    <div class="my-order-content" :style="style">
          <section class="my-order-list" v-for="(item,key) in order.list">
            <div class="order-dp">
              <header class="my-order-list-storeName"><i></i>{{item.storeName}}<span class="order-state">{{item.orderStatus}}</span></header>
              <div class="sp-contanier" v-for="(item1,key1) in item.items">
                <div class="hi-life-list-item" @click="viewSp(item1.orderNo)">
                  <div class="hi-life-list-item-img"><img :src="item1.imageUrl"></div>
                  <div class="hi-life-list-item-content">
                    <div class="hi-life-list-item-summary">{{item1.summary}}</div>
                    <div class="hi-life-list-detail"><span class="sp-sales">{{item1.sellPrice|formarMoney}}</span><span class="sp-count">x{{item1.amount}}</span></div>
                  </div>
                </div>
              </div>
              <div class="sp-count-detail">共{{item|size}}件商品，合计{{item.payMoney}}</div>
            </div>
          </section>
    </div>
  </div>
</template>

<script>
  import {getOrderList} from '../../service/getData'
  import tab from '@/components/tab/tab'
  import header from '@/components/header/header'
  export default {
    components: {
      'tab-view': tab,
      'header-view': header
    },
    data () {
      return {
        title: '我的订单',
        fixed: false,
        routerName: 'custom',
        order: {},
        tabAction: [
          {id: 'all', categoryName: '全部'},
          {id: 'waitpay', categoryName: '待付款', state: '1'},
          {id: 'pay', categoryName: '已付款', state: '3'},
          {id: 'handlerefund', categoryName: '退款中', state: '2'},
          {id: 'refund', categoryName: '已退款', state: '0'},
          {id: 'success', categoryName: '交易完成', state: '4'}
        ],
        style: {
          height: ''
        },
        active: 'all'
      }
    },
    created: function () {
      this.style.height = document.body.clientHeight - 90 + 'px' // 计算menu的高度
    },
    mounted: function () {
      var _this = this
      getOrderList()
        .then(res => {
          _this.order = res.data
        })
        .catch(function () {
          _this.$toast('加载失败!')
        })
    },
    filters: {
      formarMoney: function (val) {
        return '¥' + parseFloat(val).toFixed(2)
      },
      size: function (item) {
        return 1
      }
    },
    methods: {
      changeTab: function (val) {
        let _this = this
        this.active = val
        if (val === 'all') {
          getOrderList()
          .then(res => {
            _this.order = res.data
          })
          .catch(res => {
            _this.$toast('加载失败')
          })
        } else {
          getOrderList(val)
          .then(res => {
            _this.order = res.data
          })
          .catch(res => {
            _this.$toast('加载失败')
          })
          }
      },
      viewSp: function (val) {
        this.$router.push({
          name: 'orderDetail',
          query: {
            'orderNo': val
          }
        })
      }
    }
  }
</script>

<style>
/* 我的订单页面*/

.my-order-view {
  height: 100%;
  background-color: #F5F5F5;
}

.my-order-list-storeName {
  height: .45rem;
  color: #333;
  padding-right: .15rem;
  line-height: .45rem;
  border-bottom: .01rem solid #E5E5E5;
}

.my-order-list-storeName>i {
  width: .15rem;
  height: .15rem;
  margin-right: .05rem;
  display: inline-block;
  vertical-align: sub;
  background: url(/static/image/shopping_cart_store@3x.png);
  background-size: 100% 100%;
}

.order-state {
  float: right;
  color: rgb(255, 161, 38);
}

.order-dp {
  position: relative;
  margin-bottom: .05rem;
  background-color: #FFFFFF;
}

.my-order-list .order-dp:after {
  content: '';
  position: absolute;
  height: .05rem;
  bottom: 0;
  right: 0;
  left: -.15rem;
  background-color: #F5F5F5;
}

.my-order-list .order-dp:last-child:after {
  background-color: #FFFFFF;
}

.my-order-list {
  background-color: #FFFFFF;
  padding-left: .15rem;
}

.my-order-list:last-child {
  margin-bottom: 1rem;
}

.my-order-content {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.my-order-content .hi-life-list-item {
  padding: 0;
}

.sp-count-detail {
  height: .45rem;
  padding-right: .15rem;
  line-height: .45rem;
  text-align: right;
}

.hi-life-list-detail {
  position: absolute;
  left: .15rem;
  right: .15rem;
  bottom: .16rem;
}

.hi-life-list-detail span.sp-count {
  float: right;
}

.order-dp .hi-life-list-item-summary,
.order-dp .sp-sales,
.sp-count-detail {
  color: #333;
}
.scorll-tab {
  border-bottom: .01rem solid #E5E5E5;
  background: white;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.scorll-tab .my-tab {
  display: block;
  border-bottom: none;
  white-space: nowrap;
}

.scorll-tab .my-tab div {
  display: inline-block;
  padding: 0 .215rem;
  height: 100%;
  background-color: #FFFFFF;
}
</style>


